<template>
  <div>
    <ul class="grid grid-cols-3 gap-3 mt-8 px-6 row-gap-5">
      <li v-for="option in options" :key="option.id" class="w-full">
        <BaseButton
          class="big note-picker mx-auto button-shadow"
          :class="{
            selected: option == pickedNote,
            ok: note == pickedNote && option == pickedNote,
            correct: pickedNote != null && pickedNote != note && option == note,
          }"
          :disable-button="pickedNote != null"
          @click="$emit('pickNote', option)"
        >
          {{ option.baseNote }}
          <div v-if="option.isFlat" class="flat-mark text-lg">b</div>
        </BaseButton>
      </li>
    </ul>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'NoteOptions',
  props: {
    note: {
      type: Object,
      required: true,
    },
    optionsQty: {
      type: Number,
      required: true,
    },
    pickedNote: {
      type: Object,
      required: false,
    },
  },
  data() {
    return {
      options: [],
    }
  },
  created() {
    // Pick some prev and next notes. FIXME: Refactor this!
    for (let index = 1; index < Math.floor(this.optionsQty) + 1; index++) {
      let tmpNote = this.getNoteById(this.note.id + index)
      if (tmpNote) this.options.push(tmpNote)
      tmpNote = this.getNoteById(this.note.id - index)
      if (tmpNote) this.options.push(tmpNote)
    }
    // Suffle, pick (quantity - 1) and then add the correct note. Then suffle again
    this.options = this.options.sort(() => 0.5 - Math.random()).slice(0, this.optionsQty - 1)
    this.options.push(this.getNoteById(this.note.id))
    this.options.sort(() => 0.5 - Math.random())
  },
  computed: {
    ...mapGetters(['getNoteById']),
  },
}
</script>

<style lang="scss">
// FIXME: Removed /deep/ and scoped
.note-picker {
  width: 5.2rem;
  height: 5.2rem;
  display: flex;
  align-items: center;
  justify-content: center;
