  @include transition;
  &.big {
    font-size: 1.5rem;
    padding: 1rem 2.5rem;
  }
  &.selected {
    &,
    &:hover {
      background: #8e8e8e;
    }
    transform: translateY(2px);
    box-shadow: none;
  }
  &.ok {
    &,
    &:hover {
      background: var(--accent-color);
    }
  }
  &.correct {
    &,
    &:hover {
      animation: blink 0.5s ease-in infinite alternate;
    }
  }
}
.flat-mark {
  margin-right: -0.3rem;
  position: relative;
  top: 0.31rem;
  left: 0.1rem;
}

@keyframes blink {
  from {
    background-color: var(--accent-color);
  }
  to {
    background-color: var(--main-color);
  }
}
</style>
