<template>
  <main>
    <div class="bg-white">
      <div class="header mx-4 flex items-center justify-between uppercase font-light h-full">
        <div class="name">Qu nota?</div>
        <ul class="flex items-center">
          <li>Cant: {{ totalNotes }}</li>
          <li>
            <div class="separator mx-3"></div>
          </li>
          <li>Seg: {{ seconds }}</li>
        </ul>
      </div>
    </div>
    <div>
      <div class="staff-container relative bg-white pt-5 pb-8 px-8 rounded-b-big overflow-hidden">
        <staff :clef="'G'" :note="currentNote" />
        <div class="countdown pr-8" v-if="state != 'finished'">
          <div v-show="currentSeconds > 0">{{ currentSeconds }}</div>
          <div v-show="currentSeconds == 0" class="uppercase">0</div>
        </div>
      </div>
      <div class="flex flex-col items-center my-4" v-if="state == 'config'">
        <div class="font-bold text-lg my-5 uppercase">Configuracin</div>
        <div class="grid grid-cols-2 gap-6">
          <div class="flex flex-col items-center">
            <label for="total-notes" class="uppercase text-sm mb-1">Cantidad</label>
            <input
              v-model.number="totalNotes"
              type="number"
              step="1"
              min="5"
              name="total-notes"
              @blur="totalNotes < 1 ? (totalNotes = 1) : null"
              class="w-20 text-2xl text-center lg:pl-3 py-3 border border-gray-600 bg-white rounded"
              :disabled="state != 'config'"
            />
          </div>
          <div class="flex flex-col items-center">
            <label for="seconds" class="uppercase text-sm mb-1">Seg. por nota</label>
            <input
              v-model.number="seconds"
              type="number"
              step="1"
              min="1"
              name="seconds"
              @blur="seconds < 1 ? (seconds = 1) : null"
              class="w-20 text-2xl text-center lg:pl-3 py-3 border border-gray-600 bg-white rounded"
              :disabled="state != 'config'"
            />
          </div>
        </div>
        <BaseButton @click="nextNote" class="accent button-shadow mt-5">
          Empezar!
        </BaseButton>
      </div>
      <div v-if="state == 'playing'">
        <NoteOptions
          v-if="currentNote && state != 'finished'"
          :note="currentNote"
          :pickedNote="pickedNote"
          :optionsQty="6"
          @pickNote="pickNote"
          :key="newComp"
        />
        <div class="grid items-center justify-end px-10 mt-6 h-16" style="grid-template-columns:.6fr 1fr;">
          <div class="mr-6 text-2xl font-thin text-accent">{{ excerciseNotes.length }} / {{ totalNotes }}</div>
          <transition :name="state == 'playing' ? 'fade-x' : ''">
            <div v-if="pickedNote != null || currentNote == null" class="flex justify-end">
              <BaseButton @click="nextNote" class="accent button-shadow">
                {{ nextNoteButtonLabel }}
                <template v-if="state == 'playing'">
                  <img
                    src="@/assets/images/icons/arrow.svg"
                    alt
                    style="width: 1.3rem; margin-left: 1rem; position:relative; top: .05rem"
                  />
                </template>
              </BaseButton>
            </div>
          </transition>
        </div>
      </div>
      <div v-if="state == 'finished'" class="flex flex-col items-center pt-8 px-4 w-full">
        <div class="font-bold text-xl">Terminaste!</div>
        <ul class="text-center">
          <li class="mt-2">
            Acertaste {{ correctExcerciseNotes.length }} de {{ totalNotes }} notas ({{ effectiveness }}%).
          </li>
          <li v-if="uniqueWrongExcerciseNotes.length > 0" class="mt-2">
            Tens que practicar:
            <div class="flex flex-wrap justify-center">
              <div
                v-for="(wrong, index) in uniqueWrongExcerciseNotes"
                :key="index"
                class="mx-3 text-3xl font-bold note-styled"
              >
                {{ wrong | styleFlat }}
              </div>
            </div>
          </li>
        </ul>
        <BaseButton @click="restart" class="accent button-shadow mt-4">
          Reiniciar
          <img
            src="@/assets/images/icons/restart.svg"
            alt
            style="width: 1.3rem; margin-left: .8rem; position:relative;"
          />
        </BaseButton>
      </div>
    </div>
    <div class="flex flex-col justify-center bg-gray-bg">
      <div
        class="flex items-center justify-between font-light text-sm py-3 mx-4 border-t h-full"
        style="border-color: #ccc"
      >
        <ul class="flex items-center">
          <li>
            <label class="font-bold">Racha:</label>
            {{ correctExcerciseNotes.length }}/{{ totalNotes }}
          </li>
          <li>
            <div class="separator mx-3"></div>
          </li>
          <li>
            <label class="font-bold">Efectividad:</label>
            {{ effectiveness }}%
          </li>
        </ul>
        <BaseIconButton @click="restart()" class="w-5">
          <img src="@/assets/images/icons/restart-b.svg" alt="Reiniciar" />
        </BaseIconButton>
      </div>
    </div>
  </main>
</template>

<script>
import { Howl } from 'howler'
import { mapGetters } from 'vuex'
import { mapState } from 'vuex'
import Staff from '@/components/Staff.vue'
import NoteOptions from '@/components/NoteOptions.vue'

function initialState() {
  return {
    excerciseNotes: [],
    currentNote: null,
    pickedNote: null,
    totalNotes: 20,
    state: 'config',
    newComp: 1,
    seconds: 5,
    noteTimeout: null,
    currentSeconds: null,
  }
}

export default {
  name: 'PickNoteExcercise',
  components: {
    Staff,
    NoteOptions,
  },
  data() {
    return initialState()
  },
  created() {
    //this.currentNote = this.randomNote()
  },
  methods: {
    pickNote(pickedNote) {
      clearTimeout(this.noteTimeout)
      this.pickedNote = pickedNote
      if (this.playSounds) {
        let chime, volume
        if (this.currentNote == this.pickedNote) {
          chime = 'correct.wav'
          volume = 0.5
        } else {
          chime = 'error.mp3'
          volume = 0.2
        }
        this.sound = new Howl({
          src: require('@/assets/sounds/' + chime + ''),
          volume: volume,
        }).play()
      }

      this.excerciseNotes.push({
        note: this.currentNote,
        picked: this.pickedNote,
        correct: this.currentNote.id == this.pickedNote.id,
      })
    },
    nextNote() {
      if (this.state == 'config') this.startExcercise()

      this.pickedNote = null

      if (this.excerciseNotes.length == this.totalNotes) {
        this.finishExcercise()
      } else {
        this.newComp++
        this.currentNote = this.randomNote()
        this.currentSeconds = this.seconds
        this.notePickCountdown()
      }
    },
    startExcercise() {
      this.state = 'playing'
    },
    finishExcercise() {
      this.state = 'finished'

      this.sound = new Howl({
        src: require('@/assets/sounds/chime.mp3'),
        volume: 0.5,
      }).play()
    },
    notePickCountdown() {
      if (this.currentSeconds > 0) {
        this.noteTimeout = setTimeout(() => {
          this.currentSeconds -= 1
          this.notePickCountdown()
        }, 1000)
      } else {
        this.pickNote({ id: 0 })
      }
    },
    restart() {
      clearTimeout(this.noteTimeout)
      //let res = options.confirm ? confirm('Ests seguro?') : true
      Object.assign(this.$data, initialState())
    },
    uniqueArray(names) {
      let unique = {}
      names.forEach(function(i) {
        if (!unique[i]) {
          unique[i] = true
        }
      })
      return Object.keys(unique)
    },
  },
  computed: {
    ...mapState(['playSounds']),
    ...mapGetters(['randomNote']),
    correctExcerciseNotes: function() {
      return this.excerciseNotes.filter(level => level.correct) || []
    },
    wrongExcerciseNotes: function() {
      return this.excerciseNotes.filter(level => !level.correct) || []
    },
    uniqueWrongExcerciseNotes: function() {
      /*let list = (this.excerciseNotes.filter(level => !level.correct) || []).map( note => note.note.name )
      let unique = list.filter()
      return unique*/
      let unique = (this.excerciseNotes.filter(level => !level.correct) || []).map(note => note.note.name)
      return this.uniqueArray(unique)
    },
    effectiveness: function() {
      const num =
        this.excerciseNotes.length == 0 ? 0 : (this.correctExcerciseNotes.length * 100) / this.excerciseNotes.length
      return num.toFixed(0)
    },
    updateStaff: function() {
      // Used to skip the update of the staff when last item is reached, avoiding the note sound. FIXME: Refactor?
      return this.excerciseNotes.length == this.totalNotes ? this.totalNotes - 1 : this.excerciseNotes.length
    },
    nextNoteButtonLabel: function() {
      switch (this.excerciseNotes.length) {
        case 0:
          return 'Empezar!'
        case this.totalNotes:
          return 'Terminar'
        default:
          return 'Siguiente'
      }
    },
  },
  filters: {
    styleFlat: function(value) {
      return value.replace(/[()]/g, '')
    },
  },
}
</script>

