import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    playSounds: false,
    instrument: {
      type: 'piano',
      totalNotes: 88,
      firstNote: {
        name: 'A',
        octave: 0,
        baseNote: 'A',
        isFlat: false,
      },
      hasFlatNotes: true,
    },
    NOTE_LIST: ['C', 'D(b)', 'D', 'E(b)', 'E', 'F', 'G(b)', 'G', 'A(b)', 'A', 'B(b)', 'B'],
    BASE_NOTES: ['C', 'D', 'E', 'F', 'G', 'A', 'B'],
    notes: [],
  },
  mutations: {
    TOGGLE_SOUNDS(state) {
      state.playSounds = !state.playSounds
    },
    CREATE_NOTES(state, notes) {
      state.notes = notes
    },
  },
  actions: {
    toggleSounds({ commit }) {
      commit('TOGGLE_SOUNDS')
    },
    createNotes({ commit }, notes) {
      commit('CREATE_NOTES', notes)
    },
  },
  getters: {
    randomNote: state => (clef = 'G') => {
      let clefNotes
      if (clef == 'G') {
        clefNotes = state.notes.filter(
          note => note.octave == 4 || (note.octave == 5 && state.BASE_NOTES.indexOf(note.baseNote) < 6)
        )
      }
      return clefNotes[Math.floor(Math.random() * clefNotes.length)]
    },
    getNoteById: state => noteId => {
      return state.notes.find(note => parseInt(note.id) == parseInt(noteId))
    },
  },
  modules: {},
})
