<template>
  <div>
    <div id="cheat">{{ aRandomNote }}</div>
    <div class="staff-container">
      <staff :clef="'G'" :note="aRandomNote" />
    </div>
    <BaseButton @click="moreRandom" class="mt-10">More</BaseButton>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { mapGetters } from 'vuex'
import Staff from '@/components/Staff.vue'

export default {
  name: 'RandomNote',
  components: {
    Staff,
  },
  data() {
    return {
      aRandomNote: null,
    }
  },
  created() {
    this.aRandomNote = this.randomNote()
  },
  methods: {
    moreRandom() {
      this.aRandomNote = this.randomNote()
    },
  },
  computed: {
    ...mapState(['BASE_NOTES', 'notes']),
    ...mapGetters(['randomNote']),
  },
}
</script>

