<template>
  <div id="music-app">
    <header class="py-5 px-4 bg-white">
      <div id="logo" class="text-lg font-bold tracking-wide text-accent">::: astronote</div>
    </header>
    <div id="nav" class="mb-5 hidden">
      <router-link :to="{ name: 'random-note' }">Random</router-link>|
      <router-link :to="{ name: 'pick-note' }">Pick note</router-link>
    </div>
    <div id="volume-controller" v-on:click="toggleSounds">
      <img src="@/assets/images/icons/volume-on.svg" alt="Silenciar" v-if="playSounds" />
      <img src="@/assets/images/icons/volume-off.svg" alt="Desilenciar" v-if="!playSounds" />
    </div>
    <router-view />
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import { mapState } from 'vuex'

export default {
  name: 'MusicApp',
  created() {
    this.createNotes(this.createInstrumentNotes())
  },
  methods: {
    ...mapActions(['toggleSounds', 'createNotes']),
    createInstrumentNotes() {
      let _this = this
      let notes = []
      notes.push({
        id: 1,
        note: this.instrument.firstNote.note,
        octave: this.instrument.firstNote.octave,
        baseNote: this.instrument.firstNote.baseNote,
        isFlat: this.instrument.firstNote.isFlat,
      })
      for (let index = 0; index < this.instrument.totalNotes - 1; index++) {
        const note = _this.nextInstrumentNote(notes[notes.length - 1], this.instrument.hasFlatNotes)
        notes.push(note)
      }
      return notes
    },
    nextInstrumentNote(currentNote, hasFlatNotes) {
      let note = {}
      note.id = currentNote.id + 1
      note.name = this.nextNote(currentNote.name, hasFlatNotes)
      note.isFlat = note.name.includes('(b)')
      note.baseNote = note.name[0]
      if (currentNote.name === 'B') {
        note.octave = currentNote.octave + 1
      } else {
        note.octave = currentNote.octave
      }
      return note
    },
    nextNote(currentNote, hasFlatNotes) {
      let notes = this.NOTE_LIST
      notes = hasFlatNotes ? notes : notes.filter(note => !note.name.includes('(b)'))
      const nextNoteIndex = notes.indexOf(currentNote) + 1
      const nextNoteName = nextNoteIndex - Math.floor(nextNoteIndex / notes.length) * notes.length
      return notes[nextNoteName]
    },
  },
  computed: {
    ...mapState(['playSounds', 'instrument', 'NOTE_LIST']),
  },
}
</script>

