<template>
  <div class="note">
    <img src="@/assets/images/flat.svg" v-if="note.isFlat" class="marker flat" />
    <img src="@/assets/images/black_note.svg" class="note-img black" v-on:click="playNote" />
  </div>
</template>

<script>
import { Howl } from 'howler'
import { mapState } from 'vuex'

export default {
  name: 'Note',
  props: ['note'],
  data() {
    return {
      sound: null,
    }
  },
  created() {
    this.sound = new Howl({
      src: require('@/assets/sounds/' + this.note.octave + '/' + this.note.name + this.note.octave + '.ogg'),
      volume: 0.5,
    })
    if (this.playSounds) {
      this.playNote()
    }
  },
  destroyed() {
    this.sound.unload()
  },
  methods: {
    playNote() {
      if (this.playSounds) {
        this.sound.stop()
        this.sound.play()
        this.sound.fade(1, 0, 1000)
      }
    },
  },
  watch: {
    playSounds: function() {
      this.sound.stop()
    },
  },
  computed: {
    ...mapState(['playSounds']),
  },
}
</script>

<style lang="scss" scoped>
.note {
  position: absolute;
  bottom: -42%;
  left: 60%;
  width: 1.2rem;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  .note-img {
    object-fit: contain;
    width: 100%;
    height: 100%;
  }
}
.line.ledger .note:after {
  content: '';
  background: #000;
  width: 3.5rem;
  height: 2px;
  position: absolute;
  left: 50%;
  bottom: 11%;
  transform: translateX(-50%);
}
.flat {
  position: absolute;
  bottom: -0.18rem;
  left: -6rem;
  width: 0.8rem;
}
</style>
