import Vue from 'vue'
import VueRouter from 'vue-router'
import RandomNote from '@/views/RandomNote.vue'
import PickNoteExcercise from '@/views/PickNoteExcercise.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'random-note',
    component: RandomNote,
  },
  {
    path: '/pick-note',
    name: 'pick-note',
    component: PickNoteExcercise,
  },
]

const router = new VueRouter({
  routes,
})

export default router
